@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@300&display=swap);
html, #root {
    margin: 0;
    height: 100%;
}

body {
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{margin:0;height:100%;text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.landing_name{font-family:'MuseoModerno', cursive;display:flex;justify-content:center;align-items:center;background:#000;min-height:100vh}.landing_name h1{font-size:9vw;font-weight:200;color:#fff;padding:1vw 3vw;border:0.65vw solid #fff;border-radius:2vw;text-transform:uppercase;animation:flicker 2.5s infinite alternate}@keyframes flicker{0%,19%,21%,23%,25%,54%,56%,100%{text-shadow:-0.2rem -0.2rem 1rem #fff,0.2rem 0.2rem 1rem #fff,0 0 2rem #f0f,0 0 4rem #f0f,0 0 6rem #f0f,0 0 8rem #f0f,0 0 10rem #f0f;box-shadow:0 0 0.5rem #fff,inset 0 0 0.5rem #fff,0 0 2rem #f0f,inset 0 0 2rem #f0f,0 0 4rem #f0f,inset 0 0 4rem #f0f}20%,24%,55%{text-shadow:none;box-shadow:none}}

.landing_page{position:relative;background:black;width:100%;height:100%}

